.items--list{
    width: 100%;
    height: 100%;
    list-style: none;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.items--list span {
    width: 30%;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    font-size: 50px;
    color: #f0ffff;
}
.tbItm_sp_4, .tbItm_sp_5, .tbItm_sp_6{
    border-top: 2px solid #0000ff;
    border-bottom: 2px solid #0000ff;
}
.tbItm_sp_2, .tbItm_sp_5, .tbItm_sp_8{
    border-left: 2px solid #0000ff;
    border-right: 2px solid #0000ff;
}
